let UID_INC = 0;
export function uid() {
  return Date.now().toString(32) + "-" + Math.floor(Math.random() * 16777215).toString(32) + "-" + (UID_INC++).toString(32);
}
let warning = true;
export function warn(...args) {
  warning && console.warn(...args);
}
export function disableWarning() {
  warning = false;
}
