import {
  isString,
  isUndefined,
  isObject,
  isArray
} from "../util";
export const $$ = Symbol("$$");
export function isInnerObj(v) {
  const clazz = v.constructor;
  return clazz === RegExp || clazz === Date || clazz === Boolean;
}
export function isViewModel(v) {
  return isObject(v) && $$ in v;
}
export function isPublicProperty(v) {
  return isString(v) && v.charCodeAt(0) !== 95;
}
export function getPropertyName(v) {
  if (isString(v)) {
    return v;
  }
  if (v === null) {
    return "null";
  }
  if (isUndefined(v)) {
    return "undefined";
  }
  return v.toString();
}
export function parsePropertyPath(propertyPath) {
  return isString(propertyPath) ? propertyPath.indexOf(".") > 0 ? propertyPath.split(".") : [propertyPath] : isArray(propertyPath) ? propertyPath : [propertyPath];
}
export function addParent(child, parent, property) {
  if (!child.__parents) {
    child.__parents = [];
  }
  child.__parents.push({
    core: parent,
    prop: property
  });
}
export function removeParent(child, parent, property) {
  if (!child.__parents) {
    return;
  }
  const idx = child.__parents.findIndex((item) => {
    return item.core === parent && item.prop === property;
  });
  if (idx >= 0) {
    child.__parents.splice(idx, 1);
  }
}
export function shiftParent(child, parent, property, delta) {
  if (!child.__parents)
    return;
  const item = child.__parents.find((it) => {
    return it.core === parent && it.prop === property;
  });
  if (item) {
    item.prop += delta;
  }
}
